import { lazy } from "react";
const CoursesCard = lazy(() => import("./CoursesCard"));

const Courses = ({ onClickOpenModal, activeCourses }) => {
  return (
    <section
      className={`courses container ${
        activeCourses ? "courses-click-style" : ""
      }`}
    >
      <h2 data-aos="fade-right" data-aos-duration="600" data-aos-once="true">
        Kurslar
      </h2>
      <div className="row gx-5">
        <CoursesCard onClickOpenModal={onClickOpenModal} />
      </div>
    </section>
  );
};

export default Courses;
